<template>
    <yd-layout :class="'ground'">
        <div class="mainContentFluid timeline_slider history" v-show="showFlag">
            <div class="historyList">
                <yd-flexbox>
                    <yd-flexbox-item>
                        <div class="hint">历史出差记录</div>
                    </yd-flexbox-item>
                </yd-flexbox>
                <!-- <div class="part">
                    <yd-flexbox>
                        <div class="leftWord">出差时间：</div>
                        <div class="leftWord">2017/10/12—2017/10/14</div>
                        <yd-flexbox-item>
                            <router-link to="/myApplication/">
                                <div class="moreLogo">
                                    <img src="../../../static/images/more.png">
                                </div>
                            </router-link>
                        </yd-flexbox-item>
                    </yd-flexbox>
                    <yd-flexbox>
                        <yd-timeline>
                            <yd-timeline-item>
                            </yd-timeline-item>
                            <yd-timeline-item>
                                <p>
                                    <yd-flexbox>
                                        <div class="timeline">您已提交审批</div>
                                    </yd-flexbox>
                                </p>
                            </yd-timeline-item>
                            <yd-timeline-item v-for="(item,index) in approveRecord" :key="item.taskParticipant">
                                <p>
                                    <yd-flexbox>
                                        <div class="timeline">{{item.taskParticipant}}已完成审批</div>
                                    </yd-flexbox>
                                </p>
                            </yd-timeline-item>
                        </yd-timeline>
                    </yd-flexbox>
                    <yd-flexbox>
                        <div class="leftWord">状态：</div>
                        <div class="agree">审批中</div>
                    </yd-flexbox>
                </div> -->
                <div class="part" v-for="(item,index) in historyList">
                    <yd-flexbox>
                        <div class="leftWord">出差时间：</div>
                        <div class="leftWord">{{getDay(item.startdate)}}—{{getDay(item.enddate)}}</div>
                        <yd-flexbox-item>
                            <router-link to="/myApplication/">
                                <div class="moreLogo">
                                    <img src="../../../static/images/more.png">
                                </div>
                            </router-link>
                        </yd-flexbox-item>
                    </yd-flexbox>
                    <yd-flexbox>
                        <div class="leftWord">状态：</div>
                        <div class="agree">已完成</div>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <yd-tabbar slot="tabbar" bgcolor="#fff" v-show="showFlag">
            <!-- <router-link to="/tripForm/0" :class="'toFormBtn'"> -->
            <yd-button size="large" @click.native="toForm" type="primary" bgcolor="#00B6FC" color="#fefefe">发起出差备案申请</yd-button>
            <!-- </router-link> -->
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'record',
    data() {
        return {
            approveRecord: [],
            historyList: [],
            showFlag: false
        }
    },
    mounted() {
        document.setTitle('出差审批');
        // this.timeData();
        // this.approvFlow();
        this.historyTrip();
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        // 开始时间、结束时间
        // timeData() {
        //     var vm = this;
        //     vm.get(vm.conf.portalMobile + '/oawf/httptrs/basehttp!queryFormByWorkitemID.action?', {
        //         'type': 'travelremark',
        //         'workItemID': vm.userInfo.workItemID,
        //         'personID': vm.userInfo.userid
        //     }, function (res) {
        //         console.log(res.jsonData);
        //         var data = res.jsonData;
        //         vm.startDate = data.startDate.split(' ')[0];
        //         vm.endDate = data.endDate.split(' ')[0];
        //     });
        // },
        // 审批流程
        // approvFlow() {
        //     let vm = this;
        //     vm.post(vm.conf.portalMobile + '/oawf/httptrs/basehttp!queryFlowLogByWorkItemID.action?', {
        //         'type': 'travelremark',
        //         'workItemID': vm.userInfo.workItemID
        //     }, function (res) {
        //         var data = res.jsonData;
        //         for (var i = 0; i < data.length; i++) {
        //             var item = data[i];
        //             if (item.buttonName.indexOf('审批完成') != -1) {
        //                 vm.approveRecord.push(item);
        //             }
        //         }
        //         console.log(vm.approveRecord);
        //     });
        // },
        // 历史出差
        historyTrip() {
            let vm = this;
            var promise = new Promise(function (resolve, reject) {
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/historyRecord', {
                    'type': 'travelremark',
                    'queryType': '',
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
                }, function (res) {
                    vm.historyList = res.jsonData;
                    if (vm.historyList != '') {
                        resolve(true);
                    } else {
                        resolve(false);
                    }
                });
            });
            promise.then(function (data) {
                console.log('data:' + data);
                if (data) {
                    vm.showFlag = true;
                } else {
                    vm.$router.push('/tripForm/0');
                }
            });
        },
        getDay(date) {
            let vm = this;
            // let date1 = date.split(' ')[0];
            let date1 = vm.formatDate(date, 'yyyy-MM-dd');
            return date1;
        },
        toForm() {
            this.$router.push('/tripForm/0');
        }
    }
}
</script>
<style scoped>
.recordHeight {
    border-bottom: none;
    font-size: 0.3rem;
}

.part_one {
    margin-top: 0;
}

.fluid {
    padding-top: .27rem;
}

.recordAppro {
    float: left;
    font-size: 0.32rem;
    padding-left: 0.22rem;
}

.agree {
    color: #2389df;
    font-size: 0.32rem;
}

.ground {
    background-color: #fff;
}

.yd-tabbar {
    padding: 0;
}

.yd-tabbar:after {
    border-top: none;
}

.yd-btn-block {
    height: 1rem;
    font-size: 0.3rem !important;
    margin-top: 0;
    border-radius: 0;
}

.toFormBtn {
    width: 100%;
}
</style>


